.banner{
    background-image: url('../../images/banner.jpg');
    background-size: cover;
    background-position: center center;
    color: white;
    object-fit: contain;
    height: 448px;
}

.bannerContents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.bannerTitle{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.bannerDescription{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 180px;
}

.bannerButton{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    transition: all 0.2s;
}

.bannerButton:hover{
    color: #000;
    background-color: #e6e6e6;
    
}

.bannerFadeBottom{
    margin-top: 145px;
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37, 0.61), #111);
}